import returns from '../../common/returns.vue'
export default {
	components: {
		returns
	},
	data() {
		return {
			title: '填写退货信息',
			id:'',
			express_name:'',
			express_number:''
		}
	},
	methods:{
		ascertain(){
			if(!this.express_name || !this.express_number){
				uni.showToast({
					icon:'none',
					title:'请完整填写退货信息'
				})
				return
			}
			this.service.entire(this,'post',this.service.api_root.subuser.threeuser.Delivery,{
				id:this.id,
				express_name:this.express_name,
				express_number:this.express_number,
			},function(self,res){
				console.log(res)
				uni.showToast({
					icon:'none',
					title:res.msg
				})
				if(res.code == 0){
					setTimeout(function(){
						self.common.returns(self)
					},1500)
				}
			})
		}
	},
	onLoad(e) {
		this.id = e.id
	}
	
}
